import { Injectable } from '@angular/core';
import { AdListKind } from '@core/enums';
import { CommonItem, PageMetaData } from '@core/models';
import { Ad, GetAdStatusListResponse } from '@core/models/ads.model';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({ providedIn: 'root' })
export class AdsState {
	private readonly _isLoading$ = new BehaviorSubject<boolean>(false);
	private readonly _adList$ = new BehaviorSubject<Ad[]>([]);
	private readonly _ad$ = new BehaviorSubject<Ad>(undefined);
	private readonly _adsCount$ = new BehaviorSubject<number>(0);
	private readonly _adStatusList$ = new BehaviorSubject<GetAdStatusListResponse[]>([]);
	private readonly _pageMetaData$ = new BehaviorSubject<PageMetaData>(undefined);
	private readonly _adListKind$ = new BehaviorSubject<AdListKind>(AdListKind.SIMILAR);
	private readonly _reportAdReasons$ = new BehaviorSubject<CommonItem[]>(undefined);

	get reportAdReasons$(): Observable<CommonItem[]> {
		return this._reportAdReasons$.asObservable();
	}

	get isLoading$(): Observable<boolean> {
		return this._isLoading$.asObservable();
	}

	get adList$(): Observable<Ad[]> {
		return this._adList$.asObservable();
	}

	get ad$(): Observable<Ad> {
		return this._ad$.asObservable();
	}

	get adsCount$(): Observable<number> {
		return this._adsCount$.asObservable();
	}

	get adStatusList$(): Observable<GetAdStatusListResponse[]> {
		return this._adStatusList$.asObservable();
	}

	get pageMetaData$(): Observable<PageMetaData> {
		return this._pageMetaData$.asObservable();
	}

	get adListKind$(): Observable<AdListKind> {
		return this._adListKind$.asObservable();
	}

	// TODO: This is on the previous implementation. Avoid using this for now on other places.
	getPageMetaData(): PageMetaData {
		return this._pageMetaData$.getValue();
	}

	setAdStatusList(adStatusList: GetAdStatusListResponse[]): void {
		this._adStatusList$.next(adStatusList);
	}

	setAdList(adList: Ad[]): void {
		this._adList$.next(adList);
	}

	setAd(ad: Ad): void {
		this._ad$.next(ad);
	}

	setIsLoading(isLoading: boolean): void {
		this._isLoading$.next(isLoading);
	}

	setAdsCount(count: number): void {
		this._adsCount$.next(count);
	}

	setPageMetaData(pageMetaData: PageMetaData): void {
		return this._pageMetaData$.next(pageMetaData);
	}

	setAdListKind(kind: AdListKind): void {
		this._adListKind$.next(kind);
	}

	getUpdatedAdList(): Ad[] {
		return this._adList$.getValue();
	}

	setReportAdReasons(data: CommonItem[]): void {
		this._reportAdReasons$.next(data);
	}

	addDataToList(updatedAd: Ad[]): void {
		const CURRENT_AD_LIST = [...this._adList$.getValue()];

		this._adList$.next([...CURRENT_AD_LIST, ...updatedAd]);
	}

	updateAdList(updatedAd: Ad): void {
		const NEW_AD_LIST = [...this._adList$.getValue()];
		const AD_INDEX = NEW_AD_LIST.findIndex((ad: Ad) => ad.id === updatedAd.id);

		NEW_AD_LIST[AD_INDEX] = { ...updatedAd };

		this._adList$.next([...NEW_AD_LIST]);
	}

	updateAd(updateAd: Ad): void {

		this._ad$.next({ ...this._ad$.getValue(), ...updateAd });
	}

	addAd(adList: Ad[]): void {
		this._adList$.next([...this._adList$.getValue(), ...adList]);
	}

	deleteAd(deletedAd: Ad): void {
		this._adList$.next(this._adList$.getValue().filter((ad: Ad) => ad.id !== deletedAd.id));
	}
}
